import routename from './routename';
var qs = require('qs');
var titles = require('../titles')


const goto = (page, params) => {
    let _params = qs.stringify(Object.assign({'__id': page}, params))
    let _url = window.Ziggy===undefined ? page+'.html': routename(titles[page]['route'], _params).url()
    location.assign(_url)
};

export default goto
